<template>
  <b-dropdown right no-caret>
    <template #button-content>
      <b-icon icon="wallet-fill" />
    </template>
    <b-dropdown-item-button v-if="account.length === 0" v-b-modal.modal-connect>Connect {{ chainId }}</b-dropdown-item-button>
    <b-modal v-if="account.length === 0" id="modal-connect" title="Select Connector">
      <b-button block @click="connectWallet('metamask')">MetaMask</b-button>
      <b-button block @click="connectWallet('wallet-connect')">Wallet Connect</b-button>
      <b-button block @click="connectWallet('trust-wallet')">Trust Wallet</b-button>
    </b-modal>
    <b-dropdown-item v-else target="_blank" v-bind:href="networks[chainId].scan + '/address/' + account">
      {{ account.substr(0, 6) + '...' + account.substr(38, 4) }}
    </b-dropdown-item>
    <b-modal id="modal-alert" title="Error">
      {{ message }}
    </b-modal>
  </b-dropdown>
</template>

<script>
import { myUtil } from '@/utils'
import detectEthereumProvider from '@metamask/detect-provider'
const Web3 = require('web3')

export default {
  name: 'WalletButton',
  methods: {
    connectWallet: async function (name) {
      console.log(name)
      this.$bvModal.hide('modal-connect')
      try {
        if (this.ethereum) {
          myUtil.web3 = new Web3(this.ethereum)
          this.handleNewAccount(await this.ethereum.request({ method: 'eth_requestAccounts' }))
        } else {
          this.showMessage('No wallet found!')
        }
      } catch (error) {
        console.error(error)
      }
    },
    handleNewAccount: function (accounts) {
      this.account = accounts && accounts.length > 0 ? accounts[0] : ''
      console.log('Account changed: ' + this.account)
      window.myAddress = this.account
    },
    handleNewChain: async function (chainId) {
      this.chainId = chainId
      window.myChainId = chainId
      console.log('Network changed: ' + this.chainId)
      this.handleNewAccount(await this.ethereum.request({ method: 'eth_accounts' }))
    },
    showMessage: function (message) {
      this.message = message
      this.$bvModal.show('modal-alert')
    }
  },
  async mounted () {
    this.ethereum = await detectEthereumProvider({ timeout: 30000 })
    if (this.ethereum) {
      myUtil.web3 = new Web3(this.ethereum)
      this.ethereum.autoRefreshOnNetworkChange = false
      await this.handleNewChain(await this.ethereum.request({ method: 'eth_chainId' }))
      this.handleNewAccount(await this.ethereum.request({ method: 'eth_accounts' }))
      this.ethereum.on('accountsChanged', this.handleNewAccount)
      this.ethereum.on('chainChanged', this.handleNewChain)
    }
  },
  data: function () {
    return {
      chainId: '',
      account: '',
      message: '',
      networks: myUtil.supportedNetworks,
      ethereum: null
    }
  }
}

</script>

