export const myUtil = {
  strip: function (val) {
    return val.replace(/"/g, '')
  },
  web3: {},
  supportedNetworks: {
    '0x38': {
      rpc: 'https://bsc-dataseed3.defibit.io',
      scan: 'https://bscscan.com'
    },
    '0x89': {
      rpc: 'https://rpc-mainnet.matic.quiknode.pro/',
      scan: 'https://polygonscan.com'
    }
  },
  Escape: function (val) {
    if (typeof val === 'string' || val instanceof String) {
      return val.replace(/'/g, '\\u0027')
    } else {
      return val
    }
  },
  encodeParams: function (values) {
    let params = ''

    if (values.length === 0) { return '' }

    for (let i = 0; i < values.length; i++) {
      let param = values[i]

      if (param.value !== '') {
        if (param.grp !== null) {
          const _grp = values.filter(function (x) {
            return x.grp === param.grp
          })
          let _grpParam = ''

          for (let g = 0; g < _grp.length; g++) {
            param = _grp[g]

            if (param.value[0] !== '') {
              if (g === 0) {
                if (param.type === 'tuple' || param.type === 'tuple[]') {
                  _grpParam = param.value[0]
                } else {
                  _grpParam = this.toHex(param.type, this.Escape(param.value[0]))
                }
              } else
              if (param.type === 'tuple' || param.type === 'tuple[]') {
                _grpParam = _grpParam + ',' + param.value[0]
              } else {
                _grpParam = _grpParam + ',' + this.toHex(param.type, this.Escape(param.value[0]))
              }
            }
          }

          if (i === 0) { params += _grpParam } else { params += ',' + _grpParam }

          i += _grp.length - 1
        } else {
          if (i === 0) {
            if (param.type === 'tuple' || param.type === 'tuple[]') {
              params = param.value[0]
            } else {
              params = this.toHex(param.type, this.Escape(param.value))
            }
          } else
          if (param.type === 'tuple' || param.type === 'tuple[]') {
            params = params + ',' + param.value[0]
          } else {
            params = params + ',' + this.toHex(param.type, this.Escape(param.value))
          }
        }
      }
    }

    return params
  },
  add0xForAddress: function (_address) {
    _address = _address.trim()
    if (_address.startsWith('0x') === false && _address.length === 40) {
      _address = '0x' + _address
    }
    return _address
  },
  toHex: function (type, val) {
    if (type.indexOf('bool') !== -1) {
      return JSON.parse(val)
    } else if (type.indexOf('address') !== -1) {
      if (type.indexOf('[') !== -1) {
        // check for address[] - Convenience parsing to wrap with double quotes
        // multidimensional arrays will rely on the user providing the correct input (brackets, double quotes on the
        // correct places)
        const tmp = val.toString()
          .replace('[', '')
          .replace(']', '')
          .split(',')
        let toReturn = ''
        for (let i = 0; i < tmp.length; i++) {
          if (tmp[i].length === 0) {
            toReturn = toReturn + ' ,' // let it error
          } else {
            toReturn = toReturn + "'" +
              this.add0xForAddress(tmp[i].toString()
                .replace('"', '')
                .replace("'", '')) + "',"
          }
        }
        toReturn = toReturn.slice(0, -1)
        toReturn = '[' + toReturn + ']'
        return toReturn
      } else {
        // strip any existing double quotes or single quotes
        val = val.replace('"', '').replace("'", '')
        return "'" + this.add0xForAddress(val) + "'"
      }
    } else if (type === 'string') {
      // which also means if you double quote your strings, it'll follow suit ;)
      return "'" + val + "'"
    } else if (type.indexOf('uint') !== -1 || type.indexOf('byte') !== -1) {
      if (type.indexOf('[') !== -1) {
        // check for uint[] - Convenience parsing to wrap with double quotes
        // multidimensional arrays will rely on the user providing the correct input (brackets, double quotes on the
        // correct places), exit if it is
        if (type.split(/[[1-9]]/).length === 2) {
          const tmp = val.toString().replace('[', '')
            .replace(']', '').split(',')
          let toReturn = ''

          // specifically handle empty array
          if (tmp.length === 1) {
            if (tmp[0].toString() === '') {
              return '[]'
            }
          }

          for (let i = 0; i < tmp.length; i++) {
            toReturn = toReturn + "'" + tmp[i].toString() + "',"
          }
          toReturn = toReturn.slice(0, -1)
          toReturn = '[' + toReturn + ']'
          return toReturn
        } else {
          // avoid handling complex data types, rely on user to enter them as appropriate
          return val
        }
      } else {
        // strip any existing double quotes or single quotes
        val = val.replace('"', '').replace("'", '')
        return "'" + val + "'"
      }
    } else {
      return val
    }
  }
}
